  <div class="demo-image__placeholder">
     <div class="block">
       <video-player class="video-player vjs-custom-skin"
                  ref="videoPlayer" 
                  :playsinline="true" 
                  :options="playerOptions">
        </video-player>
	</div>
    <div class="leftfont">
	   <h4>:{{videodata.videoName}}</h4>
	   <p>:{{videodata.videoContent}}</p>
    </div>
   </div>
   
	<LikeVideo v-on:tofather="sonemit" />
	
  </div>
  
